tr{
    background-color: gray;
}

th{
    border-bottom: 3px solid black;
    background-color: blue;
}

tr:nth-child(2n){
    background-color: darkslategray;
}

.popup{
    position: fixed;
    width: 80vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.247);
    z-index: 2;
    display: grid;
    place-items: center;
}

.wdw {
    height: 40vh;
    max-height: 80vh;
    width: 50vw;
    background-color: white;
}

.dasAndere {
    height: 80%;
    width: 100%;
    display: grid;
    place-items: center;
}

.btnnn {
    height: 20%;
    width: 100%;
    display: grid;
    place-items: center;
    bottom: 0;
}